h2 {
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  color: #000;
  font-size: 30px;
  line-height: 1.5;
}

h4 {
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  color: #000;
  font-size: 18px;
  line-height: 1.1;
}

p {
  font-family: "Open Sans", sans-serif;
  color: #777;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.65;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizelegibility;
}

.centerContainer {
  width: 1170px;
  height: 100%;
}

.centerElements {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.tileWhitDescriptionCenter {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.tileWhitDescriptionCenter p {
  width: 500px;
  text-align: center;
  margin-top: 20px;
}

@media only screen and (max-width: 768px) {

  h2 {
    font-size: 25px;
  }

  h4 {
    font-size: 15px;
  }

  .centerContainer {
    width: 100%;
    padding: 15px;
    box-sizing: border-box;
  }

}
